/*=============== Header ====================================================================================================================================================================*/
.header-wrapper {
  position: relative;
  z-index: 100;
  background-color: transparent;
}

.header-inner {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 60px;
}
.header-inner__logo {
  transition: transform 0.3s ease 0s;
}
.header-inner__logo a {
  text-decoration: none;
}
.header-inner__logo svg {
  max-width: 180px;
  width: 100%;
}
.header-inner__logo:hover {
  transform: scale(1.05);
}
.header-inner__logo:active {
  transform: scale(0.95);
}

/*=============== /Header ====================================================================================================================================================================*/
